@import url(https://fonts.googleapis.com/css2?family=El+Messiri:wght@400;500;600;700&family=Monoton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --safety-yellow:#F1D302;
  --baby-powder:#FDFFFC;
  --fire-red:#C1292E;
  --platinum:#E9E9E9;
  --rebecca-purple:#642CA9;
  --eerie-black:#131212;
  --onyx:#363B40;
  --green-munsell:#04A777;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* navbar  */
.navbar {background-color: #C1292E;background-color: var(--fire-red);}

.navbar a {text-decoration: none;}

.logo-t {
  background-color: #F1D302;
  background-color: var(--safety-yellow);
  color: #131212;
  color: var(--eerie-black);
  width: 65px;
  height: 65px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Monoton', cursive;
}

.logo-text {
  font-family: 'El Messiri', sans-serif;
  color: #E9E9E9;
  color: var(--platinum);
  letter-spacing: 2px;
  position: absolute;
  left: 74px;
  top: 12px;
}

.logo-ease {
  font-family: 'El Messiri', sans-serif;
  color: #E9E9E9;
  color: var(--platinum);
  letter-spacing: 6px;
  font-size: 0.6rem;
  position: absolute;
  left: 75px;
  bottom: -5px;
}


.copy-btn {
  border: none;
  color: #E9E9E9;
  color: var(--platinum);
  background-color: #04A777;
  background-color: var(--green-munsell);
  border-radius: 4px;
  overflow: hidden;   
  position: absolute; 
  right: 14px;
}

.copy-btn .btn-text {
  width: inherit;
  height: inherit;
  font-size: 0.9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -2px;
  left: 0;
  z-index: 2;
}

.copy-btn .slidebg {
  background-color: #363B40;
  background-color: var(--onyx);
  width: 150px;
  height: 150px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(-45deg);
          transform: translate(-50%, -50%) rotate(-45deg);
  z-index: 1;
}

.copy-btn .btn-text, .copy-btn .slidebg {transition:all 0.3s;}


.text-box {background-color: rgb(248, 251, 255);}

.primary-btn {
  background-color: #642CA9;
  background-color: var(--rebecca-purple);
  color: #FDFFFC;
  color: var(--baby-powder);
  margin-right: 1rem;
  margin-bottom: 1rem;
}

.primary-btn:hover {
  background-color: #52238b;
  color: #FDFFFC;
  color: var(--baby-powder);
}

.word-count strong {
  color: #04A777;
  color: var(--green-munsell);
}

.read-time strong {color: #04A777;color: var(--green-munsell);}

.footer {
  background-color: #C1292E;
  background-color: var(--fire-red);
  color: #FDFFFC;
  color: var(--baby-powder);
  position: relative;
}

.footer-line {
  width: 90vw;
  height: 1px;
  background: #fdfffc73;
  position: absolute;
  top: 40px;
  z-index: 1;
}

.footer-line + div {background: #C1292E;background: var(--fire-red); z-index: 2;}

.projects a {color: #F1D302;color: var(--safety-yellow); padding: 2px;}

.social-icon {
  cursor: pointer;
  color: #FDFFFC;
  color: var(--baby-powder);
}

.social-icon:hover {color: #FFCDBC;}

@media screen and (min-width:576px) {
  .textbox-helper {font-size: 1.5rem;}
  
  .copy-btn {
    width: 116px;
    height: 34px;
  }
}

@media screen and (max-width:575px) {
  .textbox-helper {font-size: 1rem;}

  .copy-btn {
    width: 85px;
    height: 32px;
  }
}
